// Dettagli
.lightbox-detail{
    &-title{
        color: $purple-text;
        margin-bottom: 1rem;
        @include media-breakpoint-up(md){
            margin-bottom: 3rem;
        }
    }
    .modal-body{
        @include media-breakpoint-up(sm){
            padding-top: 80px;
            padding-bottom: 80px;
        }
    }
}

// Preventivo
.lightbox-quote{
    .quote-header{
        background: $purple-bg-bright url(img/content/trip-quote-bg.svg) no-repeat left center;
        color: #fff;
        display: flex;
        align-items: center;
        p{
            margin: 0;
            &:not(.h3){
                font-size: $small-font-size;
                opacity: 0.8;
            }
        }
    }
    .modal-body{
        @include media-breakpoint-up(md){
            padding-top: 80px;
            padding-bottom: 80px;
            padding-left: 70px;
            padding-right: 70px;
        }
    }    
    .form-group-title{
        color: $purple-text;
        margin-bottom: 1rem;
    }
    .form-divider{
        margin: 1rem 0;
        @include media-breakpoint-up(md){
            margin: 2rem 0;
        }
    }
    .flexible-dates{
        line-height: 1;
        .custom-control{
            line-height: 1.6rem;
        }
        @include media-breakpoint-up(md){
            margin-top: 41px;
        }
    }
    .extra-column{
        display: flex;
        .extra-service{
            flex: 1;
            background: rgba($secondary, 0.5);
            padding: 20px;
            display: flex;
            flex-direction: column;
            margin: 1rem 0;
            .text + p{
                flex: 1;
                margin: 0;
                display: flex;
                &>*{
                    flex: 1;
                    display: flex;
                }
            }
            textarea{
                flex: 1;
            }
            @include media-breakpoint-up(md){
                padding: 40px;
                margin: 0;
            }
            h6{
	           opacity: 0.8;
               font-size: 12px;
               font-weight: 600;
                margin-bottom: 1.5rem;
            }
        }
        textarea{
            border: 0 none;
            min-height: 300px;
            background: rgba(#fff, 0.3);
        }
    }
    @include media-breakpoint-up(md){
        .fields-column .form-group, .form-divider{margin-right: 2rem;}
        .fields-column>.row:last-child .form-group{margin-bottom: 0}
    }
}
