.staff-card{
    .staff-image{
        /*height: 0;
        padding-bottom: 56%;*/
        height: 150px;
        position: relative;
        overflow: hidden;
        @include border-top-radius(2px);
        img{
            @include cover-media;
            @include centered;
        }
    }
    .staff-name{
        margin-bottom: 0.5rem;
        color: $purple-text;
    }
    .staff-description{
        font-size: 12px;
        //opacity: 0.7;
        p{margin: 0}
    }
    .staff-link{
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: $body-color;
        .ico{
            opacity: 0.2;
            font-size: 18px;
        }
    }
}