.children-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .child-age-select{
        width: 47%;
        margin-bottom: 10px;
        position: relative;
        p{margin: 0}
        .icon-child{
            position: absolute;
            top: 50%;
            left: $input-btn-padding-x;
            margin-top: -14px;
            .number{
                position: absolute;
                right: -4px;
                top: 4px;
                font-size: 6px;
                font-weight: 600;
            }
        }
        .custom-select{
            padding-left: $input-btn-padding-x*3;
        }
        .child-select-remove{
            cursor: pointer;
            position: absolute;
            @include size(15px);
            border-radius: 50%;
            color: #fff;
            background: $purple-bg-bright;
            top: -7px;
            right: -7px;
            font-size: 10px;
            text-align: center;
            line-height: 15px;
            z-index: 1;
        }
    }
    .add-child-btn{
        cursor: pointer;
        @extend .form-control;
        border-radius: 2px;
        background-color: rgba(#E1DCEA, 0.5);
        border: 0 none;
        width: 47%;
        margin-bottom: 10px;
        display: inline-flex;
        align-items: center;
        font-size: $small-font-size;
        transition: background 0.3s ease;
        .ico{
            font-size: 15px;
        }
        .ico-plus{color: $primary;}
        .ico-child{opacity: 0.5;}
        span{
            flex: 1;
            opacity: 0.5;
            font-weight: 600;
            font-weight: 600;
            text-align: center;
        }
        &:hover{
            background-color: rgba(#E1DCEA, 0.7);
        }
    }
}
