.modal{
    &>.btn-round{
        pointer-events: auto;
        position: fixed;
        top: 5px;
        right: 5px;
        z-index: 1;
        @include media-breakpoint-down(xs){
            @include size(40px);
            line-height: 38px;
        }
        @include media-breakpoint-up(sm){
            top: 22px;
            right: 90px;
        }
        @include media-breakpoint-up(md){
            right: auto;
            left: 50%;
            margin-left: 358px;
        }
        @include media-breakpoint-up(lg){
            margin-left: 533px;
        }
        /*margin-bottom: $modal-dialog-margin;
        @include media-breakpoint-up(md){
            margin-bottom: $modal-dialog-margin-y-sm-up;
        }*/
    }
}
.modal-dialog{
    text-align: center;
    .modal-content{
        text-align: left;
        @include border-radius(2px);
    }        
    .modal-header{
        @include border-top-radius(2px);
        .image-container{
            flex: 1;
            overflow: hidden;
            height: $modal-header-height;
            @include border-top-radius(2px);
            img{
                @include cover-media;
            }
            @include media-breakpoint-up(md){
                height: $modal-header-height-desktop;
            }
        }
        .text-container{
            flex: 1;
            overflow: hidden;
            padding: 20px;
            @include media-breakpoint-up(md){
                min-height: 250px;
                padding-left: 70px;
                padding-right: 70px;
            }
            .row{flex: 1;}
        }
    }
    @include media-breakpoint-only(sm){
        margin: $modal-dialog-margin-y-ipad;
    }
}
