/**
 * modules/variables
 * ---
 */

// COLORS
$body-color: #2B2B2B;
$primary: #F43B60;
$secondary: #FFD41F;
$purple-text: #3F2469;
$purple-bg: #2B1D42;
$purple-bg-bright: #462082;
$purple-bg-dark: #1B122A;
$purple-grey-light: #ECE8EF;

$theme-colors: (
  "dark": $purple-bg
);


// FONT
$font-family-sans-serif: 'Montserrat', sans-serif;
$line-height-base: 1.6;
$h1-font-size: 4rem; //60px
$h2-font-size: 2.8rem; //42px
$h3-font-size: 2rem; //30px
$h4-font-size: 1.4rem; //21px
$h5-font-size: 1rem; //15px
$headings-font-weight: bold;
$headings-line-height: 1.1 !important;
$small-font-size: 0.8333rem; //11px
$smaller-font-size: 0.75rem; //11px


// GRID
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 966px,
  lg: 1168px,
  xl: 1400px
);
$container-max-widths: (
  sm: 540px,
  md: 830px,
  lg: 1024px,
  xl: 1140px
);
$grid-gutter-width: 20px;


// BUTTONS
$btn-font-weight: 600;
$btn-font-size: 70%;
$btn-box-shadow: none;
$btn-active-box-shadow: none;
$btn-border-radius: 2px;
$input-btn-padding-y: .6rem;
$input-btn-padding-x: 1.2rem;
$btn-padding-y-lg: .8rem;
$btn-padding-x-lg: 1.2rem;
$link-hover-decoration: none;
$input-btn-focus-width: 0;
$btn-round-size: 50px;


// MODALS
$modal-inner-padding: 20px;
$modal-dialog-margin-y-sm-up: 45px;
$modal-dialog-margin-y-ipad: 40px 80px;
$modal-content-box-shadow-xs: 0 8px 32px 0 rgba(0,0,0,0.3);
$modal-content-box-shadow-sm-up: $modal-content-box-shadow-xs;
$modal-header-border-width: 0;
$modal-header-height: 150px;
$modal-header-height-desktop: 360px;
$modal-backdrop-bg: $purple-bg-dark;
$modal-backdrop-opacity: .9;
$modal-header-padding: 0;
$modal-lg: 1150px;
$modal-md: 800px;
$modal-sm: 500px;


// NAVBAR
$navbar-padding-x: $grid-gutter-width/2;


// FORMS
// Forms
$input-btn-padding-x: 1rem;
$input-bg: rgba(225,220,234,0.2);
$input-color: $body-color;
$input-border-color: rgba($purple-text, 0.5);
$input-box-shadow: none;
$input-border-radius: 2px;
$input-focus-border-color: $purple-text;
$input-height-inner: 38px;
$input-transition: border-color ease-in-out .15s;
$custom-select-padding-x: 1rem;
$custom-select-height: $input-height-inner;
$custom-select-bg: $input-bg;
$custom-select-indicator-color: $primary;
$custom-select-border-radius: $input-border-radius;
$custom-select-focus-box-shadow: $input-box-shadow;
$custom-control-indicator-bg-size: 60%;
$custom-control-indicator-checked-bg: $purple-bg-bright;
$custom-control-indicator-checked-disabled-bg: #eee;
$custom-control-indicator-active-bg: $purple-bg-bright;
$custom-control-indicator-focus-box-shadow: none;
$custom-checkbox-indicator-border-radius: $input-border-radius;

$datepickerZIndex: 1051;
$datepickerFontFamily: $font-family-sans-serif;
$datepickerFontSize: 12px;
$datepickerTextColor: (
  button: $primary,
  otherMonth: #dedede,
  otherMonthInRange: #ccc,
  disabled: #aeaeae,
  currentDate: $primary,
  common: #4a4a4a,
  dayNames: $purple-text,
  navArrows: #9c9c9c
);
$datepickerBG: (
  selected: $primary,
  selectedHover: darken($primary, 5),
  inRange: rgba($primary, .1),
  hover: #f0f0f0
);
$datepickerBorderColor: (
  nav: #efefef,
  inline: #d7d7d7,
  default: $purple-bg
);



// CARDS
$card-border-width: 0;
$card-border-radius: 2px;
$card-spacer-y: 2.4rem;
$card-spacer-x: 2rem;


// SWIPER
$swiper-img-overlay-bg: linear-gradient(229.3deg, rgba(0,0,0,0.1) 0%, rgba(0,0,0,0.5) 100%);


// PAGES
// Commons
$section-padding: 80px;
$section-padding-desktop: 220px;
$canvas-padding: 60px;
$page-padding: 80px;
$page-padding-desktop: 130px;
$page-canvas-height: 250px;
$page-canvas-height-desktop: 500px;
// Trip
$trip-header-height: 80px;
$trip-header-height-desktop: 80px;
$trip-extra-spacing: 40px;
$trip-extra-spacing-medium: 60px;
$trip-extra-spacing-desktop: 80px;
$trip-map-breakpoint: 866px;


// LIGHTGALLERY
$lg-path-fonts: 'fonts';
$lg-path-images: 'img/lightgallery';
$lg-sub-html-color: rgba(#fff, 0.8);


// Tooltips

$tooltip-color: $primary;
$tooltip-bg: #fff;
$tooltip-opacity:1;
$tooltip-padding-y: .5rem;
$tooltip-padding-x: .8rem;


