.hp-slideshow {
	@include cover-el;
	overflow: hidden;
    .slides {
        position: absolute;
        width: 100%;
        height: 100%;
    }
    .slide {
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;
        opacity: 0;
        pointer-events: none;
        display: flex;
        flex-direction: column;
        align-content: center;
        justify-content: center;
        align-items: center;
    }
    
    .slides--images{
        &:after{
            content: "";
            @include cover-el;
            background: $swiper-img-overlay-bg;
            pointer-events: none;
        }
        &:before{
            content: "";
            @include cover-el;
            background: rgba($purple-bg, 0.6);
            z-index: 1;
            pointer-events: none;
        }
    }

    .slide--current {
        opacity: 1;
        pointer-events: auto;
    }

    .slide__img {
        position: absolute;
        top: -200px;
        left: -200px;
        width: calc(100% + 400px);
        height: calc(100% + 400px);
        background-size: cover;
        background-position: 50% 50%;
    }


    .shape {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        pointer-events: none;
        z-index: 1;      
    }
}


