/**
 * modules/fonts
 * ---
 * Declare fonts here
 */


h1, .h1{
    line-height: $headings-line-height;
    @include media-breakpoint-down(xs){
        font-size: 1.8rem;
    }
    @include media-breakpoint-up(xl){
        font-size: 4.8rem;
    }
}

h2, .h2{
    @include media-breakpoint-down(xs){
        font-size: $h3-font-size;
    }
}

h3, .h3{
    @include media-breakpoint-down(xs){
        font-size: $h4-font-size;
    }
}

h4, .h4{
    @include media-breakpoint-down(xs){
        font-size: $h5-font-size;
    }
}

small, .small{
    //line-height: 1.9;
}

.unordered-list{
    list-style: none;
    padding: 0;
    margin: 0;
    li {
        position: relative;
        padding-left: 1rem; 
        font-size: 14px;
        margin-bottom: 1rem;
        &:before {
            position: absolute;
            left: 0;
            top: 7px;
            content: "";
            @include size(5px);
            border-radius: 50%;
            background-color: $primary;
        }      
    }
}