.partner-page{
    .partner-list{
        margin-top: 4rem;
    }
}

.tec-director-focus{
    @include media-breakpoint-up(xl){
        padding: 2rem 4rem;
    }
    @include media-breakpoint-down(sm){
        margin-top: 2rem;
    }
    text-align: center;
    .card-title{
        margin-top: 1.5rem;
        margin-bottom: 1.5rem;
        color: $purple-text;
    }
    .card-text{
        opacity: 0.7;
        font-size: $small-font-size;
        font-weight: 500;
    }
    .ico-timone{
        font-size: 25px;
    }
}