.map-path-container{
    display: none;
    align-items: center;
    justify-content: center;    
    @include media-breakpoint-up(md){
        width: 15%;
        display: flex;
        flex-direction: column;
    }
    @include media-breakpoint-up(lg){
        width: 25%;
    }
    .map-path-title{
        font-size: $small-font-size;
        color: $primary;
        text-transform: uppercase;
        font-weight: bold;
        margin-bottom: 80px;
    }
    .map-path{
        position: relative;        
        .path-bg{
            position: relative;
            @include size(79px, 512px);
            overflow: hidden;
            opacity: 0.3;
            img{
                position: absolute;
            }
        }
        .path-filled{
            @include cover-el;
            bottom: auto;
            height: 0;
            overflow: hidden;
        }
        .map-pins{
            @include cover-el;
            .map-pin{
                cursor: pointer;
                box-sizing: content-box;
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                @include size(24px);
                border: 1px solid #fff;
                border-radius: 50%;
                background: #fff;
                color: $purple-bg;
                transition: color 0.3s ease;
                &.map-pin-active{
                    color: $primary;
                    &:after{animation: scaleShadow 2s infinite;}
                }
                &--extra.map-pin-active{
                    color: $purple-bg;
                    background: $secondary;
                    border-color: $secondary;
                    &:before{
                        background: $secondary;
                    }
                }
                &:before, &:after{
                    content: "";
                    @include cover-el;
                    background: #fff;
                    border-radius: 50%;
                    transform: scale(1);
                    transition: transform 0.3s ease;
                }
                &:after{
                    opacity: 0.3;
                }
                @keyframes scaleShadow{
                    50%{
                        opacity: 0.3;
                    }
                    100%{
                        transform: scale(2.5);
                        opacity: 0;
                    }
                }
                .ico{
                    position: relative;
                    z-index: 1;
                    font-size: 13px;
                }
                &:hover:before{
                    transform: scale(1.3);
                }
                &:nth-child(1){
                    top: -12px;
                    left: 13px;
                }
                &:nth-child(2){
                    top: 105px;
                    left: 48px;
                }
                &:nth-child(3){
                    top: 197px;
                    left: 24px;
                }
                &:nth-child(4){
                    top: 293px;
                    left: 39px
                }
                &:nth-child(5){
                    top: 403px;
                    left: -9px;
                }
                &:nth-child(6){
                    top: 495px;
                    left: 17px;
                }
            }
        }
    }
}


.map-swiper{
    //background: $purple-bg-dark;
    flex: 1;
    .swiper-slide{
        display: flex;
    }
    .map-slide{
        flex: 1;
        display: flex;
        flex-direction: column-reverse;
        .map-image{  
            background: $purple-bg-dark;
            flex: 1;
            @media (max-width: $trip-map-breakpoint - 1){
                height: 190px;
                overflow: hidden;
                img{
                    @include cover-media;
                }
            }
        }
        .map-info{
            background: $purple-bg-dark;
            flex: 1;
            box-shadow: -20px -20px 100px 0 rgba(255,255,255,0.03);
            color: rgba(255,255,255,.9);
            padding: 25px;
            .btn-link{color: #fff;}
            .content{
                max-width: 250px;
                position: relative;
            }
            .stage-abstract small{
                line-height: 1.6;
                display: block;
            }
        }
        @media (min-width: $trip-map-breakpoint){
            flex-direction: row;
            .map-info{
                flex: initial;
                position: relative;
                overflow: hidden;
                width: 40%;
                display: flex;
                align-items: center;
                justify-content: center;
                .slide-number{
                    position: absolute;
                    bottom: 60%;
                    left: 0;
                    opacity: 0;
                    transform: translate3d(-120px,0,0);
                    color: $purple-grey-light;
                    font-size: 250px;
                    font-weight: bold;
                    letter-spacing: -15px;
                    line-height: 150px;
                }
            }
            .map-image{
                height: calc(100% - 160px);
                flex: initial;
                width: 60%;
                overflow: hidden;
                position: relative;
                img{
                    @include cover-media;
                }
            }
        }      
    }
    @media (min-width: $trip-map-breakpoint){
        .map-info{
            h4, .stage-abstract, .btn{
                opacity: 0;
                transform: translate3d(0,20px,0);
            }
            .stage-abstract{transform: translate3d(0,30px,0);}
            .btn{transform: translate3d(0,40px,0);}
        }
        .map-image{
            img{
                transform: scale(1);
                transition: transform 1s ease;
            }
        }
        .swiper-slide-active{
            pointer-events: auto;
            .map-info{
                .slide-number{
                    opacity: 0.1;
                    transform: translate3d(-90px,0,0);
                    transition: all 1s ease;
                }
                h4, .stage-abstract, .btn{
                    opacity: 1;
                    transform: translate3d(0,0,0);
                    transition: all 1s ease;
                }
            }
            .map-image img{
                transform: scale(1.1);
            }
        }
    }
    @media (max-width: $trip-map-breakpoint - 1){
        .swiper-slide{
            width: 70%;
            will-change: opacity;
            &:after{
                content: "";
                @include cover-el;
                background-color: rgba(70,32,130,0.5);
                opacity: 1;
                transition: opacity 0.3s ease;
            }
            &.swiper-slide-active:after{
                opacity: 0;
                pointer-events: none;
            }
        }
    }
    .map-navigation-container{
        @media (min-width: $trip-map-breakpoint){
            position: absolute;
            left: 0;
            bottom: 60px;
            width: 40%;
            display: flex;
            align-items: center;
            justify-content: center;
            .map-navigation{
                width: 250px;
            }
        }
        .map-navigation span{
            @extend .navigation-btn;
            @include size(40px);
            display: inline-flex;
            z-index: 2;
            & + span{margin-left: 10px;}
        }
    }
}



