$icomoon-font-path: "fonts";


@font-face {
  font-family: 'Hoppete';
  src:
    url('#{$icomoon-font-path}/Hoppete.ttf?lo8ut5') format('truetype'),
    url('#{$icomoon-font-path}/Hoppete.woff?lo8ut5') format('woff'),
    url('#{$icomoon-font-path}/Hoppete.svg?lo8ut5#Hoppete') format('svg');
  font-weight: normal;
  font-style: normal;
}

.ico {
    display: inline-block;
    font-family: 'Hoppete' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: middle;
}



.ico-timone:before {
  content: "\e900";
}
.ico-arrow-up:before {
  content: "\e901";
}
.ico-pin:before {
  content: "\e902";
}
.ico-play:before {
  content: "\e903";
}
.ico-pause:before {
  content: "\e904";
}
.ico-share:before {
  content: "\e905";
}
.ico-star:before {
  content: "\e906";
}
.ico-close:before {
  content: "\e907";
}
.ico-fb:before {
  content: "\e908";
}
.ico-instagram:before {
  content: "\e909";
}
.ico-calendar:before {
  content: "\e90a";
}
.ico-child:before {
  content: "\e90b";
}
.ico-gallery:before {
  content: "\e90c";
}




.ico-play-inverted{
    transform: rotate(180deg);
    &:before{
        content: "\e903";
    }
}

.ico-plus {
    transform: rotate(45deg);
    &:before {
        content: "\e907";
    }
}

.ico-arrow-right {
    transform: rotate(90deg);
    &:before {
        content: "\e901";
    }
}
