.trip-extra{
    &>.container{
        padding-top: $trip-extra-spacing;
    }
    @include media-breakpoint-up(md){
        &>.container{
            padding-top: $trip-extra-spacing-desktop;
        }
        &>.row>div{
            display: flex;
            align-items: center;
        }
    }
}


.extra-container{
    overflow: hidden;
    position: relative;
    top: $trip-extra-spacing;
    margin-bottom: $trip-extra-spacing;
    @include media-breakpoint-up(md){
        top: $trip-extra-spacing-desktop;
        margin-bottom: $trip-extra-spacing-desktop;
    }
    .container{position: relative;}
    .extra-swiper-overflow{
        width: 100vw;
        overflow: hidden;
    }
    .extra-swiper{
        margin: 0;
        overflow: visible;
        position: static;        
        @include size(100%, 420px);
        &.slider-swiper{
            @include size(310px, 420px);
        }
        @include media-breakpoint-up(md){
            @include size(100%, 600px);
            &.slider-swiper{
                @include size(864px, 600px);
            }
        }
        .swiper-slide{
            &:after{
                content: "";
                @include cover-el;
                background-color: rgba(70,32,130,0.5);
                will-change: opacity;
                opacity: 0;
                pointer-events: none;
                transition: opacity 0.3s ease;
            }            
            @include media-breakpoint-up(md){
                &.swiper-slide-active .trip-extra-item:hover{
                    .extra-image img{
                        transform: scale(1.1);
                    }
                }
            }
            
        }
        &.slider-swiper{
            .swiper-slide{
                &:after{
                    opacity: 1;                    
                }
                &.swiper-slide-active:after{
                    opacity: 0;
                }
                &:not(.swiper-slide-active){
                    .trip-extra-item{pointer-events: none;}
                }
                @include media-breakpoint-down(sm){
                    width: 90%;
                }
            }
        }
        .trip-extra-item{
            @include cover-el;
            overflow: hidden;
            .extra-image{
                @include cover-el;
                img{
                    @include cover-media;
                    transform: scale(1);
                    transition: transform 2s ease;
                }
                &:after{
                    content: "";
                    @include cover-el;
                    background: linear-gradient(0deg, #000 0%, rgba(0,0,0,0.2) 100%);
                }
            }
            .extra-content{
                position: absolute;
                left: $trip-extra-spacing;
                bottom: $trip-extra-spacing + 10;
                right: $trip-extra-spacing;
                color:rgba(255,255,255,.9);
                p{margin: 0;}
                .btn{margin-top: 1rem;}
                @include media-breakpoint-up(md){
                    left: $trip-extra-spacing-desktop;
                    bottom: $trip-extra-spacing-desktop;
                    right: $trip-extra-spacing-desktop;
                    padding-right: 40%;
                    .btn{
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        margin-top: 3rem;
                    }
                }
            }            
        }
    }
}

