.btn{
    cursor: pointer;
    position: relative;
    font-size: $btn-font-size;
    text-transform: uppercase;
    overflow: hidden;
    &:before{
        content: "";
        @include cover-el;
        right: 100%;
        background: #fff;
        transition: right 0.6s ease;
    }
    span{
        position: relative;
    }
    &:hover{
        background: inherit;
        &:before{right: 0}
    }
    & + .btn{margin-left: 10px;}
}



// Color

.btn-light{
    @include button-variant(#fff, transparent, #fff, transparent);
    @include hover{
        border-color: $purple-grey-light;
    }
    &:not([disabled]):not(.disabled):active,
    &:not([disabled]):not(.disabled).active,
    .show > &.dropdown-toggle {
        border-color: $purple-grey-light;
    }
}
.btn-light, 
.btn-outline-dark{
    color: $primary;
    &:before{
        background: $purple-grey-light;
    }
    @include hover{
        background: #fff;
        color: $primary;
    }
    &:not([disabled]):not(.disabled):active,
    &:not([disabled]):not(.disabled).active,
    .show > &.dropdown-toggle {
        background: #fff;
        color: $primary;
    }
}
.btn-primary{
     &:before{
        background: darken($primary, 10%);
    }
    &:hover{
        background: $primary;
    }
}
.btn-secondary{
     &:before{
        background: darken($secondary, 8%);
    }
    &:hover{
        background: $secondary;
    }
}



// Type

.btn-link{
    position: relative;
    font-weight: $btn-font-weight;
    color: $body-color;
    padding-left: 0;
    overflow: visible;
    padding-right: 0;
    &:hover{text-decoration: none;}
    &:before{display: none;}
    &:after{
        content: "";
        position: absolute;
        top: 90%;
        left: 0;
        right: 0;
        height: 2px;
        margin-top: -2px;
        background: $primary;
        transform: translate3d(0,0,0);
        transition: transform 0.3s ease;
    }
    &.btn-lg{
        &:after{
            height: 4px;
            margin-top: -4px;
            border-radius: 2px;
        }
        &:hover:after{
            transform: translate3d(0,7px,0);
        }
    }
    &--white{
        color: #fff;
        opacity: 0.8;
        &:hover{color: #fff}
        &:after{background: #fff}
    }
    &:hover:after{
        transform: translate3d(0,4px,0);
    }
}

.btn-round{
    @include size($btn-round-size);
    padding: 0;
    line-height: $btn-round-size - 2;
    text-align: center;
    border-radius: 50%;
    transform: scale(1);
    transition: transform 0.3s ease;
    .ico{
        font-size: 16px;
    }
    &:before{display: none}
    &:hover{
        transform: scale(1.1);
    }
}
.number-indicator{
    pointer-events: none;
    box-shadow: 0 0px 10px 0 rgba(0,0,0,0.1);
    font-size: $font-size-base;
    &:hover{
        transform: none;
    }
}





// Size

.btn-lg{
    border-radius: $btn-border-radius;
    font-weight: $btn-font-weight;
    &.btn-link{
        text-transform: none;
        font-size: $font-size-lg;
        font-weight: normal;
    }
}

.btn-sm{
    font-size: $font-size-sm;
}


