/**
 * partials/footer
 * ---
 */


.main-footer {
    background: $purple-bg-dark;
    color: rgba(#fff, 0.6);
    font-size: $small-font-size;
    padding: 80px 0;
    position: relative;
    @include media-breakpoint-up(md){
        padding: 150px 120px;
    }
    .logo-footer{opacity: 0.2}
    .btn-link{
        font-size: $small-font-size;
        text-transform: none;
        padding-top: 0;
        line-height: 1.1;
        border-top: 0;
    }
    .footer-block{
        margin-bottom: 40px;
        h4{
            font-size: $small-font-size;
            color: #fff;
        }
    }
    .back-to-top {
        position: absolute;
        top:-22px;
        right:10%;
        z-index: 2;
        //opacity: 0;
        //pointer-events: none;
        transition: all .3s ease;
        .btn-round {
            box-shadow: 0 2px 10px 0 rgba(0,0,0,0.05);
            width:55px;
            height: 55px;
            line-height: 50px;
        }
        .ico {
            color:$primary;
        }
        /*&.visible {
            opacity:1;
            pointer-events: auto;
        }
        &.sticky {
            position: absolute;
            bottom: auto;
            top:-22px;
        }*/
    }
}


.footer-credits{
    background: darken($purple-bg-dark, 5%);
    color: rgba(#fff, 0.6);
    padding: 1rem 0;
    font-size: $font-size-sm;
    a{
        color: #fff;
        display: block;
        opacity: 0.8;
        transition: opacity 0.3s ease;
        &:hover{opacity: 1}
        img{display: block}
    }
    p{
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        span{margin-right: 5px;}
        .emoji{margin: 0 5px!important;}
    }
}


