.trip-categories-container{
    &.page-content{padding-bottom: 0;}
    position: relative;
    z-index: 2;
}
.trip-categories{
    //height: 100vh;
    @include media-breakpoint-up(md){
        display: flex;
        position: relative;
        top: 80px;
    }
    .swiper-slide{
        img{
            @include centered;
            @include cover-media;
            transform: translate3d(-50%, -50%, 0) scale(1);
            transition: transform 1s ease;
        }
    }
    @include media-breakpoint-down(sm){
        .categories-swiper{
            position: relative;
            top: 40px;
        }
        .swiper-slide{
            @include size(85%, 400px);
            border-bottom: 4px solid transparent;
            &:after{
                content: "";
                @include cover-el;
                background-color: rgba(70,32,130,0.5);
                pointer-events: none;
                opacity: 1;
                will-change: opacity;
                transition: opacity 0.3s ease;
            }
            &.swiper-slide-active{
                border-color: $primary;
                &:after{
                    opacity: 0;
                }
            }
            &:before{
                content: "";
                @include cover-el;
                will-change: opacity;
                background: linear-gradient(0deg, #000 0%, rgba(0,0,0,0.2) 100%);
                z-index: 1;
            }
        }
    }
    @include media-breakpoint-up(md){
        .categories-swiper{
            background: $purple-bg-dark;
            flex: 40%;
            box-shadow: 0 2px 34px 0 rgba(0,0,0,0.2);
            display: flex;
            .swiper-wrapper{
                flex: 1;
                height: auto;
                .swiper-slide {
                    height: auto;
                }
            }
        }
        .swiper-slide{
            &.swiper-slide-active img{
                transform: translate3d(-50%, -50%, 0) scale(1.1);
            }
        }
    }    
    .categories-swiper-navigation{
        flex: 60%;
        margin-top: 15px;
        margin-bottom: 80px;
        position: relative;
        z-index: 1;
    }
    .categories-swiper-pagination{
        bottom: 20px;
    }
    .category-item{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        cursor: pointer;
        display: flex;
        justify-content: center;
        padding: 40px 40px 60px;
        transition: background 0.3s ease;     
        z-index: 1;
        @include media-breakpoint-up(md){
            position: relative;
            padding: 70px;
            background: $purple-bg;
        }
        .category-icon{
            flex: none;
            @include size(120px);
            background: url(img/content/categories/ico-relax-off.svg) no-repeat 0 0;
            background-size: 100%;
            transition: background 0.3s ease;
            @include media-breakpoint-up(md){
                margin-right: 60px;
                @include size(80px);
            }
            &.icon-natura{
                background-image: url(img/content/categories/ico-natura-off.svg);
            }
            &.icon-cultura{
                background-image: url(img/content/categories/ico-cultura-off.svg);
            }
            @include media-breakpoint-down(sm){
                &.icon-relax{background-image: url(img/content/categories/ico-relax.svg);}
                &.icon-natura{background-image: url(img/content/categories/ico-natura.svg);}
                &.icon-cultura{background-image: url(img/content/categories/ico-cultura.svg);}
            }
        }
        .category-inner{
            flex: 1;
            max-width: 380px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            @include media-breakpoint-up(md){
                flex-direction: row;
            }
        }
        .category-title{     
            transition: color 0.3s ease;
            @include media-breakpoint-down(sm){
                text-align: center;
                margin: 25px 0;
            }
        }
        .category-description{
            font-size: $small-font-size;
            font-weight: 500;
            @include media-breakpoint-up(md){
                opacity: 0.8;
            }
        }
        &:after{
            content: "";
            height: 100%;
            width: 4px;
            background: $primary;
            position: absolute;
            top: 0;
            left: -4px;
            transform: scaleY(0);
            transition: transform 0.3s ease;
        }
        &:not(:last-child){
            border-bottom: 2px solid $purple-bg-dark;
        }
        &.active{
            background: $purple-bg-dark;
            &:after{
                transform: scaleY(1);
                transition-duration: 1s;
            }
            .category-title{
                color: $primary;
            }
            .icon-relax{background-image: url(img/content/categories/ico-relax.svg);}
            .icon-natura{background-image: url(img/content/categories/ico-natura.svg);}
            .icon-cultura{background-image: url(img/content/categories/ico-cultura.svg);}
        }
    }
    @include media-breakpoint-up(lg){
        .categories-swiper-navigation{
            flex: 40%;
        }
        .categories-swiper{
            flex: 60%;
        }
    }
}