.main-canvas{
    position: relative;
    min-height: 500px;
    height: 100vh;
    display: flex;
    color: #fff;
    @include media-breakpoint-up(sm){
        min-height: 600px;
    }
    .canvas-swiper{
        background: $purple-bg;
        @include cover-el;
        .swiper-slide{
            img{
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
            &:after{
                content: "";
                @include cover-el;
                background: $swiper-img-overlay-bg;
                pointer-events: none;
            }
        }
    }
    &>.container{
        position: relative;
        z-index: 2;
        display: flex; 
        align-items: center;
        &>.row{
            flex: 1;
        }
    }
    .canvas-footer{
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: $trip-header-height;
        padding-left: $grid-gutter-width;
        display: flex;
        justify-content: space-between;
        align-items: center;
        box-shadow: 0 2px 14px 0 rgba(0,0,0,0.1);
        z-index: 2;
        .canvas-pagination{
            flex: 1;
        }
        .social-btns{
            padding-right: $canvas-padding;
            &>.share-btn{
                cursor: pointer;
                display: flex;
                align-items: center;
                color: #fff;
                transition: color 0.3s ease;
                span{font-size: $small-font-size;}
                &:hover{
                    color: $primary;
                }
            }
            .share-btn + .share-btn{margin-left: 7px;}
        }
        .music-player{
            background: #fff;
            align-self: stretch;
            padding:0 25px;
            color: $body-color;
            flex: 1;
            display: flex;
            align-items: center;
            .btn{
                flex: none;
            }
            .small{
                margin-left: 1rem;
                line-height: 1.6;
            }
            @include media-breakpoint-up(md){
                flex: initial;
                max-width: 230px;
            }
        }
        @include media-breakpoint-up(sm){
            bottom: 0;
        }
        @include media-breakpoint-up(md){
            padding-left:$canvas-padding;
            padding-right: 20px;
            bottom: 20px;
            box-shadow: none;
        }
    }
    .canvas-pagination{
        display: flex;
        justify-content: flex-start;
        .canvas-pagination-bullet{
            display: flex;
            align-items: center;
            justify-content: flex-start;            
            cursor: pointer;
            .number{
                @include size(30px);
                text-align: center;
                border-radius: 50%;
                border: 1px solid rgba(#fff, 0.2);
                line-height: 30px;
                font-size: 11px;
                font-weight: 600;
                margin-right: 15px;
                position: relative;
                transition: border-color 0.3s ease;
                svg{
                    position: absolute;
                    left: -1px;
                    top: -1px;
                    transform: rotate(-90deg);
                    stroke: #fff;
                }
                &:hover{
                    border-color: rgba(#fff, 0.6);
                }
            }
            .image-caption{
                overflow:hidden;
                white-space: nowrap;
                opacity: 0.8;
                font-size: 11px;
                font-style: italic;
                box-sizing: content-box;
                //flex: 0;
                max-width: 0;
                transition: all 0.5s ease;
            }
            &-active{
                .image-caption{
                    max-width: 250px;
                    padding-right: 12px;
                    transition-delay: 0.5s;
                    transition-duration: 1s;
                }                
            }
        }
    }
    .scroll-down-text{
        position: absolute;
        right: 50px;
        top: 50%;
        margin-top: -40px;
        padding-top: 50px;
        @include size(3px, 80px);
        z-index: 1;
        span{
            position: absolute;
            left: 50%;
            bottom: 100%;
            transform: translate3d(-50%, 0, 0) rotate(-90deg);
            color: #FFF;
            font-size: $small-font-size;
            font-weight: 500;
            text-transform: uppercase;
            white-space: nowrap;
        }
    }
    @include media-breakpoint-only(md){
        height:92vh;
    }
}

.scroll-down-icon {
    width: 25px;
    height: 40px;
    z-index: 2;
    margin: 10px 0 0 -10px;
    svg {
        width: 25px;
        opacity: 1;
        transition: opacity .5s;
    }
    .stroke {
        stroke: rgba(#fff, 0.5);
    }
    .scroller {
        fill: #fff;
        animation: updown 1s infinite;
    }
    @keyframes updown {
        0% {
            transform: translate(0, 0)
        }
        50% {
            transform: translate(0, 5px)
        }
        100% {
            transform: translate(0, 0)
        }
    }
}

//transitions for canvas 

.trip-canvas {
    .swiper-wrapper .swiper-slide img {
        opacity:0;
        position: relative;
        animation: imageZoom 1s forwards;
    }
    .tour-leader {
        opacity:0;
        animation: appearing 800ms 1s forwards;
    }
    h1 {
        opacity:0;
        animation: appearing 800ms 1.2s forwards;
    }
    .trip-detail {
        opacity:0;
        animation: appearing 800ms 1.4s forwards;
    }
    .tour-buttons {
        opacity:0;
        animation: appearing 800ms 1.6s forwards;
    }
    .canvas-footer {
        opacity: 0;
        animation: appearing 800ms 1.8s forwards;
    }
    .scroll-down-text {
        opacity: 0;
        animation: appearing 800ms 2s forwards;
    }
}

@keyframes appearing {
    0% {
        opacity: 0;
        transform: translateY(10%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes imageZoom {
    0% {
        opacity: 0;
        transform: scale(1) translateY(10%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0);
    }
}







