.form-control{
    font-size: $small-font-size;
    font-weight: 500;
    &::placeholder {
        font-style: italic;
    }
    &[readonly]{
        background: $input-bg;
    }
}

.custom-select {
    font-size: $small-font-size;
    font-weight: 500;
}

.form-group{
    label{
        opacity: 0.8;
        font-size: 12px;
        font-weight: 600;
        line-height: 21px;
    }
}

.custom-control-label::before{
    border: 1px solid $input-border-color;
}
.custom-checkbox .custom-control-input:active ~ .custom-control-label::after {
    background-image: $custom-checkbox-indicator-icon-checked;
}

.datepicker{
    background: #f9f8fb;
    .datepicker--cell{
        border-radius: 0;
    }
    .datepicker--cell.datepicker--cell-day.-weekend-:not(.-selected-):not(.-in-range-){
        background: rgba($secondary, .2);
    }
}




// CONTACT FORM 7

.cf7-submit-container{
    position: relative;
    .ajax-loader{
        position: absolute;
        top: 100%;
        margin: 5px 0 0 0!important;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
    }
}

.form-group{
    .wpcf7-not-valid-tip{
        @extend .invalid-feedback;
        display: block;
    }
}

div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing{
    font-size: $small-font-size;
    border: 0 none;
    padding: 1rem 2rem;
    color: $form-feedback-invalid-color;
    margin-left: 0;
    margin-right: 0;
    background-color: rgba($form-feedback-invalid-color, .2);
}

div.wpcf7-mail-sent-ok{
    font-size: $small-font-size;
    border: 0 none;
    padding: 1rem 2rem;
    color: darken($form-feedback-valid-color, 10%);
    margin-left: 0;
    margin-right: 0;
    background-color: rgba($form-feedback-valid-color, .2);
}

span.wpcf7-list-item{
    margin-left: 0
}

.wpcf7-acceptance{
    label{
        opacity: 0.8;
        font-size: 12px;
        font-weight: 600;
        line-height: 21px;
    }
}

.wpcf7-checkbox, .wpcf7-acceptance{
    input{
        display: none;
        &:checked ~ .wpcf7-list-item-label{
            &:before{
                background-color: $custom-control-indicator-checked-bg;
            }
            &:after{
                background-image: $custom-checkbox-indicator-icon-checked;
            }
        }
    }
    label{
        position: relative;
        display: block;
        min-height: 1.6rem;
        padding-left: 1.5rem;
        margin-bottom: 0;
    }
    .wpcf7-list-item-label{
        &:before{
            position: absolute;
            top: 0.2rem;
            left: 0;
            display: block;
            @include size($custom-control-indicator-size);
            pointer-events: none;
            content: "";
            -webkit-user-select: none;
               -moz-user-select: none;
                -ms-user-select: none;
                    user-select: none;
            background-color: $custom-control-indicator-bg;
            border-radius: $custom-checkbox-indicator-border-radius;
            border: 1px solid $input-border-color;
        }
        &:after{
            position: absolute;
            top: 0.2rem;
            left: 0;
            display: block;
            @include size($custom-control-indicator-size);
            content: "";
            background-repeat: no-repeat;
            background-position: center center;
            background-size: $custom-control-indicator-bg-size; 
        }
    }
}
