.organic-item {
	position: relative;
	width: 100%;
	cursor: default;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	-webkit-tap-highlight-color: rgba(0,0,0,0);
}

.item__svg {
	position: relative;
	display: block;
	//width: 80%;
	height: auto;
	//min-height: 500px;
}

