.tour-leader{
    display: flex;
    align-items: center;
    &-img{
        border-radius: 50%;
        @include size(50px);
        border: 2px solid #fff;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
            display: block;
        }
        &--small{
            @include size($btn-round-size+4);
        }
        &--large{
            @include size(180px);
            border: 0 none;
        }
    }
    &-info{
        margin-left: 20px;
        line-height: 1.2;
        .name{
            display: block;
            margin: 0;
            font-weight: 700;
        }
        .label{
            font-style: italic;
            font-weight: 500;
            font-size: $smaller-font-size;
        }
    }
    @include media-breakpoint-down(xs){
        &-img{
           @include size(40px); 
        }
        .tour-leader-info{
            margin-left: 10px;
        }
    }
}


.leader-extra-container{
    //margin-top: 230px;
    padding-top: 1px;
    padding-bottom: 0;
    position: relative;
    z-index: 1;
}

.trip-leader{
    margin-top: -40px;
    margin-bottom: 80px;
    background: #fff;
    color: $body-color;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.1);
    display: flex;
    &>*{flex: 1}
    .image-container{
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 75px 0 40px;
    }
    .text-container{
        display: flex;
        align-items: center;        
        .trip-leader-content{padding: 0 $grid-gutter-width 75px;}
        .page-content-category{
            display: block;
            font-size: 1rem;
            font-style: italic;
            color: $primary;
            line-height: 1.3;
            margin-bottom: 0.5rem;
            text-transform: none;
        }
        h3{
            color: $purple-text;
            margin-bottom: 2rem;
        }
        p{margin-bottom: 0}
        
    }
    @include media-breakpoint-up(sm){
        margin-top: -230px;
        padding:0 80px;
    }
    @include media-breakpoint-up(md){        
        min-height: 460px;
        margin-bottom: 160px;
        padding:0;
        .image-container{padding: 0}
        .text-container .trip-leader-content{
            max-width: 80%;
            padding: 0;
        }
    }
    @include media-breakpoint-down(sm){
        .tour-leader-img {
            @include size(80px);
        }
    }
}
