.trip-preview{
    display: block;
    min-height: 200px;
    color: #fff;
    background: $purple-bg;
    //cursor: url(img/cursor.png), auto;
    position: relative;
    overflow: hidden;
    .trip-thumb{
        @include cover-el;
        transform: scale(1);
        transition: transform 2s ease;
        img{
            @include cover-media;            
        }
        &:after{
            content: "";
            @include cover-el;
            background: linear-gradient(180deg, rgba(0,0,0,0) 0%, rgba(0,0,0,0.7) 100%);
            pointer-events: none;
        }
    }
    &-content{
        position: absolute;
        bottom: 20px;
        right: 20px;
        left: 20px;
        z-index: 1;
        @include media-breakpoint-up(sm){
            bottom: 50px;
            right: 50px;
            left: 50px;
            .trip-detail{
                min-height: 38px;
            }
        }
        @include media-breakpoint-up(lg){
            bottom: 100px;
            right: 100px;
            left: 100px;
        }
    }
    .tour-leader-img{
        @include size(25px);
    }
    .tour-leader-info, .trip-detail{
        font-size: $small-font-size;
        font-weight: 600;
    }
    &:hover{
        color: #fff;
    }
    @include media-breakpoint-up(md){
        &:hover{
            .trip-thumb{
                transform: scale(1.1);
            }
        }
    }
}
