html{
    font-size: 15px;
}

body{
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.page-content{
    padding-top: $section-padding;
    padding-bottom: $section-padding;
    .page-content-category{
        color: $primary;
        font-size: $small-font-size;
        font-weight: 500;
        text-transform: uppercase;
    }
    .page-content-title{
        margin: 10px 0 15px;
        &--dark{
            color: $purple-text;
        }
        @include media-breakpoint-up(md){
            margin: 20px 0 45px;
        }
    }
    @include media-breakpoint-up(md){
        padding-top: $section-padding-desktop;
        padding-bottom: $section-padding-desktop;
    }
    &.page-content-sm{
        padding-top: $section-padding/2;
        padding-bottom: $section-padding/2;
        @include media-breakpoint-up(md){
            padding-top: $section-padding-desktop/2;
            padding-bottom: $section-padding-desktop/2;
        }
    }
}


.page-canvas{    
    overflow: hidden;
    position: relative;
    z-index: 2;
    height: $page-canvas-height;
    padding-top: 30px;
    @include media-breakpoint-up(md){
        padding-top: 0;
        height: $page-canvas-height-desktop;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    .canvas-image{
        background: $purple-bg;
        @include size(150%, 100%);
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate3d(-50%, 0, 0);
        border-radius: 0 0 50% 50%;
        overflow: hidden;
        @include media-breakpoint-down(xs){
            @include size(170%, 100%);
        }
        img{
            @include cover-media;
            opacity: 0.2;
        }
        &:after{
            content: "";
            @include cover-el;
        }
    }
    h1{
        position: relative;
        color: #fff;
        margin: 0;
        z-index: 1;
        b{
            font-weight: inherit;
            color: $secondary;
        }
    }
}

.page-body{
    .page-content:first-child{
        padding-top: $section-padding + 80; 
        margin-top: -80px;
        @include media-breakpoint-up(md){
            padding-top: $section-padding-desktop + 80; 
        }
    }
}