.hp-canvas{
    overflow: hidden;
    h1{
        b, strong{color: $secondary}
    }
    &-intro{
        opacity: 0.8;
        margin: 20px 0;
        font-weight: 600;
        @include media-breakpoint-up(sm){
            margin-bottom: 35px;
        }
        @include media-breakpoint-down(xs){
            font-size: 0.8rem;
        }
    }
    .scroll-down-text{
        top: auto;
        margin-top: 0;
        bottom: 60px;
        right: 60px;
    }
}

.hp-slideshow{
    &:before, &:after{
        content: "";
        pointer-events: none;
        opacity: 0;
        transition: opacity 0.3s ease;
    }
    &:before{
        @include cover-el;
        background: $purple-bg;
        z-index: 2;
    }
    &:after{
        @include size(40px);
        @include centered;
        background: url(img/icons/loading.svg) no-repeat 0 0;
        background-size: 100%;
        z-index: 3;
    }
    &.loading{
        &:before, &:after{opacity: 1;}
    }
}

.trip-preview-swiper{
    padding-bottom: 50px;
    .swiper-slide{
        display: flex;
        @include size(310px, 420px);
        @include media-breakpoint-up(sm){
            @include size(380px, 550px);
        }
        @include media-breakpoint-up(lg){
            @include size(450px, 700px);
        }
        .trip-preview{
            flex: 1;
        }
    }
}



.hp-staff{
    @include media-breakpoint-up(md){
        padding-top: 120px;
        padding-bottom: 120px;
    }
    .hp-staff-content{
        padding: 60px 0;
        @include media-breakpoint-up(md){
            padding: 140px 0;
        }
    }
    .highlights{
        display: flex;
        @include media-breakpoint-down(sm){
            margin-bottom: 2rem;
            .highlights-text{padding-top: 13px;}
        }
        .btn{
            flex: none;
        }
        @include media-breakpoint-up(md){
            flex-direction: column;
        }
    }
    .row>div:last-child>.highlights{margin-bottom: 0}
    .hoppete-swiper{
        padding-bottom: 30px;
        &.swiper-container-horizontal>.swiper-pagination-bullets{
            bottom: 0;
        }
        .swiper-wrapper{
            align-items: stretch;
        }
        .swiper-slide{
            padding: 5px;
            height: auto;
            display: flex;
            .staff-card{flex: 1}
        }
        @include media-breakpoint-down(xs){
            margin-right: -$grid-gutter-width;
            margin-left: -$grid-gutter-width;
            padding-left: $grid-gutter-width - 5;
            .swiper-slide{
                width: 90%;
            }
        }
    }
    .hoppete-swiper-navigation{
        position: absolute;
        left: 0;
        bottom: 35px;
        .navigation-btn{
            display: inline-flex;
            margin-right: 10px;
            color: #000;
            border-color: rgba(#000, 0.2);
            &:not(.swiper-button-disabled):hover{
                border-color: rgba(#000, 0.4);
            }
        }
    }
}


.partner-list{
    .partner-item{
        margin-bottom: 3rem;        
        display: flex;
        align-items: center;
        justify-content: flex-start;        
        .image{
            flex: none;
            @include size(88px);
            border-radius: 50%;
            overflow: hidden;
            border: 4px solid #fff;
            box-shadow: 0 0 6px rgba(0,0,0,0.2);
            img{@include cover-media}
        }
        .partner-tooltip{
            color: $primary;
            font-size: 22px;
            margin-right: 3px;
            cursor: pointer;            
        }
        .partner-info{
            margin-left: 1rem;
        }
        .partner-name{
            color: $purple-text;
            font-weight: bold;            
        }
        p{
            font-weight: 500;
            opacity: 0.7;
            font-size: $small-font-size;
            line-height: 1.4;
            margin-top: 3px;
        }
        @include media-breakpoint-up(lg){
            padding-right: 4rem;
        }
        @include media-breakpoint-down(xs){
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            .image{
                @include size(68px);
                border-width: 2px;
            }
            .partner-info{
                margin-top: 1rem;
                margin-left: 0;
            }
            .partner-name{                
                font-size: $small-font-size;
            }
        }
    }
}










