.has-blobs{
    overflow: hidden;
    position: relative;
    &>*{
        position: relative;
        z-index: 1;
    }
    .blob-yellow, .blob-purple{
        content: "";
        position: absolute;
        background: url(img/blobs/blob1-purple.svg) no-repeat 0 0;
        background-size: 100%;
    }
    &--type1{
        .blob-yellow{
            background-image: url(img/blobs/blob1-yellow.svg);
            @include size(820px, 609px);
            top: -265px;
            right: -317px;
            @include media-breakpoint-down(xs){
                width: 540px;
                height: 380px;
            }
        }
        .blob-purple{
            @include size(500px, 370px);
            bottom: -161px;
            left: -245px;
        }
    }
    &--type2{
        .blob-yellow{
            background-image: url(img/blobs/blob2-yellow.svg);
            @include size(1066px, 804px);
            bottom: -418px;
            right: -347px;
            @include media-breakpoint-down(xs){
                width: 536px;
                height: 664px;
            }
        }
    }
    &--type3{
        .blob-purple{
            background-image: url(img/blobs/blob3-purple.svg);
            @include size(1069px, 816px);
            top: -605px;
            right: -608px;
            @include media-breakpoint-down(xs){
                width: 900px;
                height: 690px;
            }
        }
        .blob-yellow{
            background-image: url(img/blobs/blob3-yellow.svg);
            @include size(657px, 470px);
            bottom: -161px;
            left: -264px;
            @include media-breakpoint-down(xs){
                height: 300px;
            }
        }
    }
    &--type4{
        .blob-yellow{
            background-image: url(img/blobs/blob4-yellow.svg);
            @include size(1082px, 745px);
            bottom: -383px;
            left: -349px;
            @include media-breakpoint-down(xs){
                height: 545px;
            }
        }
    }
}