.trip-canvas{
    .trip-data{
        font-weight: 700;
        //font-size: $smaller-font-size;
        .tile{margin: 0 10px;}
    }
    .canvas-footer{
        bottom: -20px;
        padding-left: 0;
        @include media-breakpoint-up(md){
            bottom: 20px;
            padding-left:$canvas-padding;
        }
    }
}

.trip-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $trip-header-height;
    z-index: 3;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-shadow: 0 2px 14px 0 rgba(0,0,0,0.1);
    transform: translate3d(0,-100%, 0);
    transition: transform 0.5s ease;
    .music-player{
        align-self: stretch;
        box-shadow: 0 2px 14px 0 rgba(0,0,0,0.1) inset;
        align-items: center;
        justify-content: center;
    }
    .trip-info{
        flex: 1;
        display: flex;
        align-items: center;
        .trip-detail{
            padding-left: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            line-height: 1.5;
            }
        .trip-data{
            font-weight: 600;
            font-size: 0.83rem;
            opacity: 0.8;
        }
    }
    .share-btn{
        cursor: pointer;
        display: inline-flex;
        align-items: center;
        color: $purple-bg;
        transition: color 0.3s ease;
        &:hover{color: $primary;}
        span{
            font-size: $small-font-size;
            font-weight: 500;
        }
    }
    &>*{
        padding: 0 20px;
    }
    @include media-breakpoint-up(sm){
        height: $trip-header-height-desktop;
        &>*{padding: 0 25px;}
    }
}

.music-player{
    .btn-round{
        .ico-pause{display: none;}
        .ico-playing{display: none;}
        &.playing{
            .ico-play{display: none;}
            .ico-pause{display: none;}
            .ico-playing{display: inline-block;}
            &:hover{
                .ico-playing{display: none;}
                .ico-pause{display: inline-block;}
            }
        }
    }
}

.trip-header--visible .trip-header{
    transform: translate3d(0,0, 0);
}

.trip-overview{
    overflow: hidden;
    position: relative;
    .trip-summary{
        .button-container{
            position: relative;
            overflow: hidden;
            padding: 0;
            transition: padding 0.3s ease;
            .btn-link{
                opacity: 0;
                transition: opacity 0.3s ease;
            }
            &.collapsed{
                padding: 1.5rem 0;
                .btn-link{
                    opacity: 1;
                }
            }
        }
        .btn-round{
            position: fixed;
            left: 50%;
            bottom: 30px;
            z-index: 3;
            transform: translate3d(-50%,180%,0);
            transition: transform 0.3s ease;
        }
    }
    .trip-content{
        &.in-viewport{
            .button-container{
                .btn-round{transform: translate3d(-50%,0,0);}
                &.collapsed{
                    .btn-round{
                        transform: translate3d(-50%,180%,0);
                    }
                }
            }
        }
    }
    .trip-description{
        b, strong{
            color: $primary;
        }
        @include media-breakpoint-up(sm){
            display: block!important;
        }
    }
}


.mosaic-lightgallery{
    @include media-breakpoint-down(sm){
        margin-top: $trip-extra-spacing;
    }
    .embed-responsive-item{
        display: flex;
        .row{flex: 1;}
    }
    .row{
        margin-left: -$grid-gutter-width/4;
        margin-right: -$grid-gutter-width/4;
        &>div{
            height: 100%;
            display: flex;
            padding-left: $grid-gutter-width/4;
            padding-right: $grid-gutter-width/4;
            &>*{flex: 1;}
        }
    }
    .image-thumbnail{
        background: $purple-bg;
        display: block;
        overflow: hidden;
        margin-bottom: $grid-gutter-width/2;
        img{
            @include cover-media;
            transform: scale(1);
            transition: transform 4s ease;
        }
        &:hover img{transform: scale(1.1);}
    }
    .col-5{
        flex-direction: column;
        &>a:first-child{
            flex: 2;
        }
    }
    .more-btn{
        position: relative;
        &:after{
            content: "";
            @include cover-el;
            background: $purple-bg;
            opacity: 0.7;
            transition: opacity 0.3s ease;
        }
        &-text{
            color: #fff;
            @include centered;
            z-index: 1;
            font-size: 18px;
        }
        &:hover:after{
            opacity: 0.4;
        }
    }
}

.trip-itinerary-container{
    padding-bottom: 0;
    @media (min-width: $trip-map-breakpoint){
        margin-bottom: 80px;
        .trip-itinerary{top: 80px;}
    }
}
.trip-itinerary{
    display: flex;
    position: relative;
    margin-top: 20px;
    @media (min-width: $trip-map-breakpoint){
        margin-top: 0;
        height: calc(100vh - 80px);
        align-items: center;
        .map-swiper{
            align-self: stretch;            
        }
        .map-slide{
            align-items: center;
            .map-info{align-self: stretch;}
        }
    }
}

.trip-tips{
    @include media-breakpoint-up(sm){
        padding-bottom: $section-padding-desktop*2;
    }
    .tip-item{
        position: relative;
        margin: 2rem 0;
        @include media-breakpoint-up(md){
            margin: 0;
            max-width: 90%;
        }
        .h4{
            color: $primary;
            margin-bottom: 1rem;
            @include media-breakpoint-up(md){margin-top: 2rem;}
        }
        .tip-text{
            font-size: 0.86rem;
            line-height: 1.9;
        }
        @include media-breakpoint-up(md){
            .h4{font-size: 1.6rem}
            .tip-text{max-width: 75%;}
        }
    }
    .row>div:nth-child(2){
        @include media-breakpoint-up(md){
            margin-top: 110px;
        }
    }
    .row>div:last-child{
        @include media-breakpoint-up(md){
            margin-top: -80px;
        }
    }
}

.trip-body.no-extra{
    .trip-tips{
        padding-bottom: 0;
        .blob-yellow{display: none;}
    }    
    .trip-inclusions .blob-purple{display: none;}
}


.trip-inclusions{
    h4{color: $purple-text}
    .included-list-container{
        margin-top: 2rem;
        &:last-child {
            margin-bottom: 6rem;
        }
        ul {
            list-style: none;
            padding: 0;
            margin: 0;
            li {
                position: relative;
                padding-left: 1rem; 
                font-size: 14px;
                margin-bottom: 1rem;
                &:before {
                    position: absolute;
                    left: 0;
                    top: 7px;
                    content: "";
                    @include size(5px);
                    border-radius: 50%;
                    background-color: $primary;
                }      
            }
        }
        em, i{
            color: $primary;
            font-style: normal;
            font-weight: 500;
        }
    }
    @include media-breakpoint-only(sm){
        padding-bottom: $section-padding-desktop;
    }
    @include media-breakpoint-up(md){
        padding-top: $section-padding-desktop + 80; 
        margin-top: -80px;
        z-index: 0;
        .blob-purple{top: -525px;}
        .included-list-container:not(.quotation-container){
            column-count: 2;
            column-gap: 50px;
        }
    }
}

.included-list-focus {
    margin: 3rem 0 4rem;
    @include media-breakpoint-down(xs) {
        margin-left: -$grid-gutter-width;
        margin-right: -$grid-gutter-width;
        padding-left: $grid-gutter-width;
        padding-bottom: 30px;
        .swiper-slide{
            width: 95%;
            padding: $grid-gutter-width $grid-gutter-width/2 $grid-gutter-width 0;
            display: flex;
            height: auto;
            .focus{
                flex: 1;
                margin: 0
            }
        }
    }
    @include media-breakpoint-up(sm) {
        overflow: visible;
        .swiper-wrapper{
            display: block;
            pointer-events: none;
            transform: none!important;
        }
    }
    .focus {
        padding: 20px;
        background: #fff;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);
        margin-bottom: 1.5rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include media-breakpoint-up(sm) {
            flex-direction: row;
            padding: 30px;
        }
        @include media-breakpoint-up(lg) {
            margin-right: 2rem;
            padding: 40px;
        }
        img {
            width: 73px;
            margin-right: 2rem;
            @include media-breakpoint-down(xs) {
                //width: 140px;
                margin-bottom: 1rem;
                margin-right: 0;
            }
        }
        .text{
            flex: 1;
            span {
                display: block;
                margin: 0 auto;
                color: $primary;
                font-weight: 600;
                text-transform: uppercase;
                margin-bottom: 0.5rem;
                @include media-breakpoint-down(xs) {
                    font-size: $small-font-size;
                    text-align: center;
                }
            }
            p{
                font-size: $small-font-size;
                margin-bottom: 0;
                @include media-breakpoint-down(xs) {
                    font-size: $small-font-size * 0.9;
                    text-align: center;
                }
            }
        }
        
    }
}


.quote-container{
    //margin-top: 150px;
    position: relative;
    z-index: 1;
    padding-top: 1px;
    padding-bottom: 0;
    hr{
        margin: $trip-extra-spacing-desktop 0 $trip-extra-spacing-medium;
        @include media-breakpoint-up(md){
            margin-top: $trip-extra-spacing-medium*2;
        }
    }
    .contacts-info{
        padding-bottom: $trip-extra-spacing-medium*2;
        text-align: center;
        .h3{
            margin-bottom: $trip-extra-spacing-medium;
        }
        @include media-breakpoint-up(md){
            padding-bottom: 160px;
            .h3{
                margin-bottom: $trip-extra-spacing-desktop;
            }
        }        
    }
}

.agencies-adv{
    border-top: 1px solid rgba(#fff, 0.2);
    margin-top: $trip-extra-spacing-medium;
    padding-top: $trip-extra-spacing-medium;
    p{
        max-width: 400px;
        margin: 0 auto;
        &.text{
            margin-top: 2rem;
            margin-bottom: 2rem;
        }
    }
}

.trip-quote{
    background: #fff;
    color: $body-color;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.1);
    display: flex;
    padding: 55px $trip-extra-spacing;
    margin-top: -150px;
    .row{
        flex: 1;
        align-items: center;
    }
    .quote-title{color: $purple-text;}
    .quote-text{
        //font-size: $small-font-size;
        margin: 2rem 0;
    }
    @include media-breakpoint-up(sm){
        padding: 60px;
        min-height: 300px;
        .quote-text{margin: 2rem 0}
    }
    @include media-breakpoint-up(md){
        padding: 55px 80px;
    }
}

.category-icons-wrapper {
    margin-top: 3rem;
    padding: 2rem 0 1rem;
    border: 1px solid $gray-200;
    border-width: 1px 0;
    h4{margin-bottom: 2rem;}
    @include media-breakpoint-up(lg){
        padding: 4rem 0 1rem;
        display: flex;
        h4{
            flex: none;
            margin: 17px 3rem 0 0;
        }
    }
    .category-icons-container{
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
    }
    .category-icons-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-right: 1rem;
        margin-bottom: 1rem;
        img{width: 60px;}
        @include media-breakpoint-down(xs){
            img{width: 50px;}
        }
        @include media-breakpoint-up(sm){
            margin-right: 1.5rem;
        }
        .trip-category-label {
            text-transform: capitalize;
            font-size: $small-font-size;
            font-weight: 500;
            color:$primary;
            @include media-breakpoint-up(sm){
                font-size: $h5-font-size;
            }
        }
    }
    @include media-breakpoint-up(md){
        margin-top: 6rem;
    }
}


