.slider-navigation{
    position: absolute;
    right: 100%;
    margin-right: 15px;
    top: 50%;
    @media (min-width: 1500px){
        margin-right: 80px;
    }
    &>span{
        @extend .navigation-btn;
        & + span{margin-top: 22px;}
        .progress-circle{
            position: absolute;
            left: -1px;
            top: -1px;
            circle{
                stroke: $secondary;
            }
        }
    }
}

.navigation-btn{
    color: #fff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    @include size(55px);
    border-radius: 50%;
    border: 1px solid rgba(#fff, 0.5);
    position: relative;
    //transform: scale(1);
    transition: border 0.3s ease;
    &.swiper-button-disabled{
        opacity: 0.4;
        cursor: default;
    }
    .ico{
        opacity: 0.5;
        font-size: 11px;
    }
    &:not(.swiper-button-disabled):hover{
        border: 1px solid rgba(#fff, 0.8);
        //transform: scale(1.1);
    }
}
